* {
  margin: 0;
  padding: 0;
  outline: none !important;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  background-color: $black;
  color: $white;
  font-family: "Montserrat", sans-serif;
}
